  methods: {
    RowClick (item) {
      console.log('click', item)
    },
    RowHover (item) {
      console.log('hover', item)
    }
  },
  data () {
    return {
      dataSource: [
        {
          key: '1',
          name: '林俊杰',
          age: 21,
          phone: 13156533695
        },
        {
          key: '2',
          name: '陈奕迅',
          age: 10,
          phone: 13156533695
        },
        {
          key: '2',
          name: '张春林',
          age: 30,
          phone: 13156533695
        }
      ]
    }
  }
}
</script>



